import { Redis } from "@upstash/redis"

// Singleton pattern for Redis client
let redis: Redis | null = null

export function getRedis() {
  if (!redis) {
    redis = new Redis({
      url: process.env.UPSTASH_KV_REDIS_URL!,
      token: process.env.UPSTASH_KV_KV_REST_API_TOKEN!,
    })
  }
  return redis
}

// Type definitions
export interface Contact {
  id: string
  name: string
  email: string
  phone: string
  company: string
  position: string
  createdAt: string
  updatedAt: string
}

export interface Deal {
  id: string
  title: string
  value: number
  contactId: string
  stage: "lead" | "qualified" | "proposal" | "negotiation" | "won" | "lost"
  description: string
  createdAt: string
  updatedAt: string
}

// Contact operations
export async function getContacts(): Promise<Contact[]> {
  const redis = getRedis()
  const contactIds = await redis.smembers("contacts:all")

  if (!contactIds || contactIds.length === 0) {
    return []
  }

  const contacts = await Promise.all(
    contactIds.map(async (id) => {
      const contact = await redis.get(`contact:${id}`)
      return contact as Contact
    }),
  )

  return contacts.filter(Boolean)
}

export async function getContact(id: string): Promise<Contact | null> {
  const redis = getRedis()
  const contact = await redis.get(`contact:${id}`)
  return contact as Contact | null
}

export async function createContact(contact: Omit<Contact, "id" | "createdAt" | "updatedAt">): Promise<Contact> {
  const redis = getRedis()
  const id = `contact_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
  const now = new Date().toISOString()

  const newContact: Contact = {
    ...contact,
    id,
    createdAt: now,
    updatedAt: now,
  }

  await redis.set(`contact:${id}`, newContact)
  await redis.sadd("contacts:all", id)

  return newContact
}

export async function updateContact(id: string, updates: Partial<Contact>): Promise<Contact | null> {
  const redis = getRedis()
  const contact = await getContact(id)

  if (!contact) {
    return null
  }

  const updatedContact: Contact = {
    ...contact,
    ...updates,
    id: contact.id,
    createdAt: contact.createdAt,
    updatedAt: new Date().toISOString(),
  }

  await redis.set(`contact:${id}`, updatedContact)
  return updatedContact
}

export async function deleteContact(id: string): Promise<boolean> {
  const redis = getRedis()
  await redis.del(`contact:${id}`)
  await redis.srem("contacts:all", id)

  // Delete associated deals
  const deals = await getDeals()
  const contactDeals = deals.filter((deal) => deal.contactId === id)
  await Promise.all(contactDeals.map((deal) => deleteDeal(deal.id)))

  return true
}

// Deal operations
export async function getDeals(): Promise<Deal[]> {
  const redis = getRedis()
  const dealIds = await redis.smembers("deals:all")

  if (!dealIds || dealIds.length === 0) {
    return []
  }

  const deals = await Promise.all(
    dealIds.map(async (id) => {
      const deal = await redis.get(`deal:${id}`)
      return deal as Deal
    }),
  )

  return deals.filter(Boolean)
}

export async function getDeal(id: string): Promise<Deal | null> {
  const redis = getRedis()
  const deal = await redis.get(`deal:${id}`)
  return deal as Deal | null
}

export async function createDeal(deal: Omit<Deal, "id" | "createdAt" | "updatedAt">): Promise<Deal> {
  const redis = getRedis()
  const id = `deal_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
  const now = new Date().toISOString()

  const newDeal: Deal = {
    ...deal,
    id,
    createdAt: now,
    updatedAt: now,
  }

  await redis.set(`deal:${id}`, newDeal)
  await redis.sadd("deals:all", id)

  return newDeal
}

export async function updateDeal(id: string, updates: Partial<Deal>): Promise<Deal | null> {
  const redis = getRedis()
  const deal = await getDeal(id)

  if (!deal) {
    return null
  }

  const updatedDeal: Deal = {
    ...deal,
    ...updates,
    id: deal.id,
    createdAt: deal.createdAt,
    updatedAt: new Date().toISOString(),
  }

  await redis.set(`deal:${id}`, updatedDeal)
  return updatedDeal
}

export async function deleteDeal(id: string): Promise<boolean> {
  const redis = getRedis()
  await redis.del(`deal:${id}`)
  await redis.srem("deals:all", id)
  return true
}
